window.addEventListener('DOMContentLoaded', () => {

  // Utils
  testWebP();
  // ---------------------------------

  iosVhFix();
  // setMaskTel();

  // hidePoster();
  document.addEventListener('keyup', hidePoster);
  // Modules
  // ---------------------------------

  //       'DOMContentLoaded',     'load'
  //  load   ,      
  window.addEventListener('load', () => {
    initTabs();
    initDynamicAdaptive();
    initModals();
    const form = new Form();
    window.form = form;
    form.init();
    initPlayer();
    initCoachersCards();
    hidePoster();
    // initHover();
  });
});

